let mypopup = document.querySelector(".map__dropdowncard");
function revertChangeSVG (numOfPoint , numOfArea) {
    let pointID = '.point' + numOfPoint;
    let point = document.querySelector(pointID);
    let areaID = '.reg' + numOfArea;
    let area = document.querySelector(areaID);
    area.firstChild.nextElementSibling.setAttribute('fill' , '#F2F2F2')
    point.innerHTML = '<path fill-rule="evenodd" clip-rule="evenodd" d="M17.638 24.2868C17.8381 23.9402 18.1399 23.6656 18.491 23.4735C22.3696 21.3514 25 17.2329 25 12.5C25 5.59644 19.4036 0 12.5 0C5.59644 0 0 5.59644 0 12.5C0 17.2335 2.63106 21.3525 6.51052 23.4743C6.86173 23.6664 7.16366 23.9411 7.36382 24.2878L10.7686 30.1849C11.5384 31.5183 13.4629 31.5183 14.2327 30.1849L17.638 24.2868Z" fill="#118BDD"/><rect x="7.03125" y="7.03125" width="10.9375" height="10.9375" fill="url(#pattern0)"/><defs><pattern id="pattern0" patternContentUnits="objectBoundingBox" width="1" height="1"><use xlink:href="#image0" transform="scale(0.0714286)"/></pattern><image id="image0" width="14" height="14" xlink:href="data:image/png;base64,"/></defs>'
    hidePopup();

}

function hover (numOfArea , numOfPoint) {
     changeSVG(numOfPoint);
     let areaID = '.reg' + numOfArea;
     let area = document.querySelector(areaID);
     area.firstChild.nextElementSibling.setAttribute('fill' , '#118BDD');
     showPopup(numOfPoint);
}


/*myicon.addEventListener("mouseover", showPopup);*/

function showPopup(n) {
  let point = ".point" + n;
  let myicon = document.querySelector(point);
  let iconPos = myicon.getBoundingClientRect();
  mypopup.style.left = (iconPos.right - 170) + "px";
  mypopup.style.top = (iconPos.top + 30 + pageYOffset) + "px";
  mypopup.style.display = "block";
}

function hidePopup() {
    mypopup.style.display = "none";
}

